import React,{useEffect,useRef, useState} from "react";
import videojs from "video.js";
import "video.js/dist/video-js.css";
import 'videojs-landscape-fullscreen'

/** 判断手机机型 **/
function isAndroidOrIOS() {
  var u = navigator.userAgent;
  var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
  var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
  if (isAndroid) {
      return "android"
  }
  if (isiOS) {
      return "ios"
  }
  return false
}

export const VideoJS = ( props ) => {

  const videoRef = useRef(null);
  const playerRef = useRef(null);
  const { options, onReady, currentTime, pauseTime, ifPause, click} = props;

  useEffect(() => {
    // make sure Video.js player is only initialized once
    if (!playerRef.current) {
      const videoElement = videoRef.current;
      if (!videoElement) return;
      const player = playerRef.current = videojs(videoElement, options, () => {
        console.log("player is ready");

        if (isAndroidOrIOS() !== 'ios') {
          player.landscapeFullscreen({
            fullscreen: {
              enterOnRotate: true,
              alwaysInLandscapeMode: true
            }
          });
        }  
        player.playsinline(true)
        onReady && onReady(player);
      });
    } else {
      // you can update player here

    }
  }, [options, videoRef]);

  useEffect(() => {
    if (playerRef.current && ifPause) {
      const player = playerRef.current;
      player.pause()
    } else if (playerRef.current && pauseTime > currentTime) {
      const player = playerRef.current;
      const handleBar = function () {
        if (player.currentTime() >= pauseTime) {
          player.pause();
        } 
      };
      player.on('timeupdate', handleBar);
      player.currentTime(currentTime);
      player.play();

      player.on('mousemove',()=>{player.off('timeupdate',handleBar)})
      return () => { player.off('timeupdate',handleBar) }
    }
  }, [currentTime, pauseTime, ifPause])
  
  // useEffect(() => {
  //   const player = playerRef.current

  //   if (player) {
  //     if (ifPause) {
  //       player.pause()
  //     } else if (click) {
  //       player.currentTime(currentTime);
  //       player.play();
  //     }
  //   } 
  // }, [currentTime, pauseTime, ifPause, click])
  

  // Dispose the Video.js player when the functional component unmounts
  React.useEffect(() => {
    const player = playerRef.current;
    // console.log('dispose')
    // console.log(player.on('mousemove',()=>{console.log('move')}))
    return () => {
      if (player) {
        player.dispose();
        playerRef.current = null;
      }
    };
  }, [playerRef]);

  return (
    <div data-vjs-player>
      <video ref={videoRef} className="video-js vjs-big-play-centered" />
    </div>
  );
}

export default VideoJS;